.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 50; 
}

.modalCallback {
  position: fixed;
  opacity: 0;
  top: 0;
  left: 0;
  transition: opacity 0.5s ease;
  z-index: 200;
  top: 50%;
  left: 50%;

  &-entering {
    opacity: 1; // opacity переходу
  }

  &-entered {
    opacity: 1; // фіксація opacity
  }

  &-exiting {
    opacity: 0; // opacity переходу
  }

  &-exited {
    opacity: 0; // фіксація opacity
  }
  

  &__content {
    position: relative;
    border-radius: 10px;
    padding: 25px;
    width: 100%;
    z-index: 100;
    transform: translate(-50%, -50%);
    border: 2px solid #46A358;
    max-width: 400px;
    background: #fff;
    margin: auto;
    box-shadow: 0 0 15px 1px rgba(96, 155, 170, 0.5);
  }   

  &__title {
    margin-bottom: 1.25rem;
    font-size: 1.625rem;
  }

  &__form {
    & label:nth-child(1) {
      margin-bottom: 5px;
    }
  
    & label:nth-child(2) {
      margin-bottom: 30px;
    }

    & button {
      padding: 15px 30px;
    }
  }
 

  &__label {
    display: flex;
    flex-direction: column;
    display: block;
    text-align: left;
    font-size: 1rem;
  }

  &__input {
    border-radius: 3px;
    width: 100%;
    border: 2px solid #46A358;
    transition: 0.3s;
    padding: 10px 15px;

    &:focus {
      outline: none;
      border: 2px solid #46A358;
      box-shadow: 1px 1px 3px 0px #46A358;
    }
  }

  &__close {
    display: inline-block;
    position: absolute;
    top: -10px;
    right: -10px;
    background-color: #fff;
    border: 2px solid #46A358;
    padding: 10px;
    transition: 0.3s;
    border-radius: 50%;
    line-height: 0;
    cursor: pointer;

    &:hover {
      border-color: #46A358;
      background: #46A358;
    }

    & svg {
      width: 10px;
      height: 10px;
      transition: 0.3s;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
  }

  &__error {
    text-align: center;

    &-title {
      margin: 0 0 1.25rem;
      font-size: 1.625rem;
    }

    &-text {
      & a {
        display: inline-block;
        margin-top: 10px;
        color: #46A358;
        border-bottom: 1px solid #46A358;
      }
    }
  }
}


